@import "normalize";

*{
  margin: 0;
  padding: 0;
}

body, html {
  height: 100%;
  font-weight: 400;
  font-size: 13px;
  line-height: 19px;
  min-height: 100vh;
  font-family: Helvetica, Tahoma, Arial, STXihei, "华文细黑", "Microsoft YaHei", "微软雅黑", SimSun, "宋体", Heiti, "黑体", sans-serif;
}

a{
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.hidden{
  display: none;
}

.app-content{
  width: 1100px;
  margin: 0 auto;
  position: relative;
}

.mask{
  background: rgba(0,0,0,0.5);
  width: 100%;
  height: 100%;
}

.index-page{
  .header{
    height: 542px;
    background: url('/images/head_bg.jpg') no-repeat fixed top;
    background-size: cover;
    .evaluate-nav{
      width: 280px;
      height: 360px;
      margin-top: 70px;
      background: #fff;
      color: #041f47;
      position: relative;
      h1{
        line-height: 24px;
        font-size: 16px;
        padding-top: 20px;
        text-align: center;
        display: block;
      }
      ul{
        li{
          font-size: 15px;
          display: block;
          width: 100%;
          height: 60px;
          line-height: 60px;
          p{
            margin-left: 10px;
            span{
              float: right;
              margin-right: 10px;
            }
          }
        }
        .aboard-time{
          line-height: 30px;
          background: #f0f0f0;
        }
      }
      button{
        width: 240px;
        height: 40px;
        background-color: #fa8c55;
        position: absolute;
        top: 300px;
        margin-left: 20px;
        border: none;
        color: #fff;
        font-size: 15px;
        word-spacing: 30px;
      }
    }
  }
  .aboard-flow{
    height: 300px;
    width: 765px;
    margin: 0 auto;

    h1{
      text-align: center;
      font-size: 18px;
      width: 100%;
    }

    ul{
      overflow: auto;
      margin-right: -145px;
      li{
        margin-top: 25px;
        position: relative;
        float: left;
        width: 145px;
        margin-right: 145px;
        img{
          margin-left: 25px;
        }
        .index {
          font-size: 16px;
          position: absolute;
          left: 7px;
          width: 30px;
          font-weight: bold;
        }
        .flow-detail{
          width: 115px;
          font-size: 15px;
          float: right;
        }
      }
    }
  }
  .middle{
    height: 2560px;
    background: url('/images/bg.jpg') no-repeat scroll top;

    .consult{
      margin: 0 auto;
      position: relative;
      top: 300px;
      button{
        width: 550px;
        height: 40px;
        background-color: #fa8c55;
        border: none;
        color: #fff;
        margin: 0 auto;
        display: block;
        font-size: 15px;
        word-spacing: 30px;
      }
    }
  }
}

.top-nav-bar{
  width: 100%;
  height: 54px;
  overflow: hidden;
  background-color: #00d8b3;
  box-shadow:0 8px 18px rgba(0,0,0,.2);
  z-index: 99;
  text-align: center;

  .top-bar-logo{
    float: left;
    display: block;
    line-height:0;
    font-size: 0;
    height: 33px;
    width: 134px;
    margin-left: 15px;
    margin-top: 11px;
  }

  .top-bar-content{
    font-size: 14px;
    margin-right: -55px;
    margin-top: 15px;
    a{
      color: #7de6ca;
    }
    .title-nav-bar{
      list-style-type: none;
    }
    float: right;
    width: 715px;
    overflow: auto;
    .nav-item{
      float: left;
      margin-right: 50px;
       &.active{
         a{
           color: #ffffff;
         }
       }
    }
  }
}


#login-panel{
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 999;


  .panel{
    .close{
      display: block;
      position: absolute;
      right: 10px;
      top: 10px;
      cursor: pointer;
    }
    width: 345px;
    background: #f3f3f3 url('/images/login-logo.png') no-repeat;
    background-position: 24px 25px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .form-nav{
      margin-top: 30px;
      overflow: auto;
      ul{
        float: right;
        overflow: auto;
        margin-top: 10px;
        li{
          cursor: pointer;
          float: left;
          width: 45px;
          height: 30px;
          color: #acacac;
          &.active{
            color: #133462;
          }
        }
      }
    }
    .form{
      margin-top: 20px;
      .button{
        width: 300px;
        height: 40px;
        background: #1ddab0;
        border: none;
        color: #fff;
        margin: 0 auto;
        display: block;
        margin-bottom: 20px;
      }

      .tips{
        font-size: 14px;
        line-height: 18px;
        color: #c6c6c6;
        display: block;
        padding-left: 30px;
        margin-bottom: 10px;
      }
    }
  }

  input{
    display: block;
    width: 290px;
    height: 40px;
    padding-left: 10px;
    margin-bottom: 10px;
    margin-left: 22px;
  }
  .sms-container{
    overflow: auto;
    .sms-input{
      width: 200px;
      float: left;
    }
    .grab-verify-code{
      border: none;
      font-size: 12px;
      line-height: 40px;
      float: right;
      margin-right: 19px;
      padding: 0 10px;
    }
  }
}

.estimate-page{
  background-color: #e0e0e0;
  background-image: url("/images/estimate-bg.png");
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;

  .estimate-form{
    width: 435px;
    position: absolute;
    left: 50%;
    margin-left: -272.5px;
    margin-top: 75px;

    label{
      font-size: 14px;
      color: #a1a1a1;
      line-height: 40px;
      width: 100px;
      display: inline-block;
    }

    h1{
      font-size: 15px;
      color: #0e2d60;
      margin-left: 210px;
      margin-bottom: 30px;
    }
    .form-group{
      display: block;
      width: 100%;
      overflow: auto;
    }
    .estimate-input{
      width: 298px;
      height: 40px;
      border: none;
      margin-bottom: 12px;
      padding-left: 2px;
    }



    .estimate-select{
      background: #fff url("/images/select_arrow.png") no-repeat 98%;
      height: 40px;
      width: 280px;
      border: none;
      overflow: hidden;
      padding-left: 20px;
      float: right;
      margin-bottom: 12px;
      select{
        background: transparent;
        border: none;
        height: 40px;
        width: 298px;
        -webkit-appearance: none;
        &:focus{
          outline: none;
        }
      }
    }
    .estimate-short-select{
      background: #fff url("/images/select_arrow.png") no-repeat 98%;
      height: 40px;
      border: none;
      overflow: hidden;
      padding-left: 20px;
      width: 120px;
      float: none;
      display: inline-block;
      select{
        -moz-appearance: window;
        text-indent: 0.01px;
        text-overflow: '';
        background: transparent;
        border: none;
        height: 40px;
        width: 118px;
        -webkit-appearance: none;
        &:focus{
          outline: none;
        }
      }
    }
  }
}

.estimate-result{
  .identity{
    overflow: auto;
    line-height: 40px;
    h1{
      font-size: 18px;
      color: #6c6c6c;
      display: inline-block;
      float: left;
    }

    ul{
      margin: 0.67em 0;
      margin-left: 100px;
      float: left;
      overflow: auto;
      li{
        margin-right: 90px;
        float: left;
        cursor: pointer;
        &.active{
          color: #0e2d60;
          border-bottom: solid;
          border-width: 4px;
        }
      }
    }
  }
  .college-list{
    width: 100%;
    .college-show{
      width: 100%;
      height: 180px;
      background: #fff;
      position: relative;
      .college-badge{
        margin-left: 27px;
        margin-top: 35px;
        display: inline-block;
      }
      .college-info{
        color: #6c6c6c;
        position: absolute;
        height: 140px;
        top: 0;
        margin-top: 10px;
        display: inline-block;
        margin-left: 68px;
        width: 410px;
        header{
          display: inline-block;
          width: 265px;
          h1{
            font-size: 18px;
            color: #000;
          }
          h2{
            font-size: 14px;
            margin-top: -10px;
            color: #6c6c6c;
          }
          .ielts-and-toelf-requirement{
            position: absolute;
            bottom: 0;
          }
        }
        .college-rank-info{
          float: right;
          position: relative;
          top: 24px;
          table{
            text-align: right;
          }
          tr{
            line-height: 27px;
            td{
              padding-left: 10px;
            }
          }
        }
      }
      .view-matching-detail{
        position: absolute;
        bottom: 25px;
        right: 15px;
        .view-details-button{
          cursor: pointer;
          background: #fff;
          border: none;
          color: #55e3c4;
          font-size: 15px;
        }
      }
      .score{
        width: 100px;
        height: 100px;
        position: absolute;
        right: 20px;
        top:25px;
        border-radius: 50px;
        color: #fff;
        background: #4ae1c0;
        .content{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          p{
            &:first-child{
              font-size: 40px;
              span{
                font-size: 12px;
              }
            }
            text-align: center;
            font-size: 12px;
          }
        }
      }
    }
  }
}
#estimate-detail-pop{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  .estimate-detail{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 870px;
    height: 360px;
    background: #fff;
    table{
      width: 100%;
    }

    .close{
      position: absolute;
      right: 0;
      top: 0;
      cursor: pointer;
    }
  }
}

.college-badge{
  width:345px;
  height: 110px;
}
.estimate-button{
  width: 138px;
  color: #ffffff;
  border: none;
  background-color: #0e2d60;
  height: 30px;
  margin-left: 210px;
  margin-top: 40px;
}

.home-page{
  background: #ebebeb;
  .page-content{
    width: 100%;
    overflow: auto;
  }

  .home-slider{
    float: left;
    margin-top: 60px;
    width: 215px;
    height: 415px;
    background: #fff;
    .user-info{
      width: 100%;
    }
    ul{
      padding-top: 195px;
      width: 100%;
      li{
        margin-bottom: 30px;
        line-height: 25px;
        &.active{
          border-left: solid 3px #0e2d60;
        }
        width: 100%;
        padding-left: 75px;
        color: #0e2d60;
        font-size: 15px;
        font-weight: bold;
      }
    }
  }

  .home-content{
    float: right;
    margin-top: 60px;
    width: 876px;
    .title{
      display: block;
      background: #fff;
      height: 54px;
      padding-left: 30px;
      color: #1e1e1e;
      font-size: 15px;
      line-height: 54px;
      font-weight:bold;
    }

    .content{
      width: calc(100% - 30px);
      background: #fff;
      margin-top: 8px;
      padding-left: 30px;
      padding-top: 30px;
      padding-bottom: 30px;
      font-size: 14px;
      label{
        width: 160px;
        display: inline-block;
        line-height: 30px;
        margin-bottom: 30px;
      }

      input{
        height: 30px;
        width: 200px;
      }

      .estimate-button{
        margin: 0 0;
      }

      .message-table{
        width: 100%;
        border: none;
        border-spacing: 0 4px;
        font-size: 16px;
        .message{
          background: #fff;
          height: 108px;
          margin-top: 4px;
          border: none;
          .message-title{
            padding-left: 15px;
            width: 142px;
          }
          .message-content{
            width: 550px;
          }
          .created_at{
            font-size: 14px;
            color: #6c6c6c;
          }
          .actions{
            width: 20px;
            font-size: 14px;
            cursor: pointer;
          }
        }
      }
    }
  }
}

.footer{
  width: 100%;

}

.shanbox{
  width:500px;
  height:40px;
  background-color:#666;
  border-radius:5px;
  line-height:40px;
  text-align:center;
  position:fixed;
  top:50%;
  left:50%;
  margin-left:-250px;
  margin-top:-20px;
  z-index:999999;
  font-size:14px;
  font-weight:bold;
  color:#FFF;}


@-webkit-keyframes fadeOut {
  0% { opacity: 1; }
  50% {opacity: 1;}
100% { opacity: 0; }
}
@-moz-keyframes fadeOut {
  0% { opacity: 1; }
  50% {opacity: 1;}
100% { opacity: 0; }
}
@-o-keyframes fadeOut {
  0% { opacity: 1; }
  50% {opacity: 1;}
100% { opacity: 0; }
}
@keyframes fadeOut {
  0% { opacity: 1; }
  50% {opacity: 1;}
  100% { opacity: 0; }
}

.shanbox{
  -webkit-animation-name:fadeOut;
  -moz-animation-name:fadeOut;
  -o-animation-name:fadeOut;
  animation-name:fadeOut;

  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: ease;
  -webkit-animation-fill-mode: both;
  -moz-animation-duration: 1s;
  -moz-animation-timing-function: ease;
  -moz-animation-fill-mode: both;
  -ms-animation-duration: 1s;
  -ms-animation-timing-function: ease;
  -ms-animation-fill-mode: both;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: both;
}